.link{
    color: white;
    font-size: 1em;
    padding: 0 2em;
    text-decoration: none;
    background-color:  #2E6296;
    border: none;
}

.link:hover{
    cursor: pointer;
    color: #FE5F55;
}

.link:active, .link:focus{
    outline: none;
}

li{
    list-style-type: none;
}

#right-links li{
    border-right: 2px solid #FE5F55;
}

#right-links li:last-child{
    border-right: none;
}