@import url('https://fonts.googleapis.com/css?family=Raleway:400,500i&display=swap');
@import url('https://fonts.googleapis.com/css?family=Poppins:700&display=swap');

#profile-container{
    height: 80vh;
    padding-top: 7em;
    display: flex;
    align-items: center;
    padding: 3em 5em;
    background-color: #2E6296;
}

#profile-container .halfDivision{
    flex: 1;
}

#img-container{
    display: flex;
    max-height: 90%;
    max-width: 38%;
    justify-content: center;
}

#img-container img{
    max-height: 100%;
    max-width: 100%;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

#profile-container .halfDivision p{
    font-family: 'Poppins', sans-serif;
    font-size: 6em;
    color: white;
    margin: 0;
}

#profile-container .halfDivision #mini-container p{
    font-family: 'Poppins', sans-serif;
    font-size: 1.1em;
    color: #FE5F55;
    padding: 0.5em 0;
}

#nameTitle{
    margin-left:10em;
}

#profile-pic{
    max-width: 100%;
    height: auto;
}