@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway:400,500i&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins:700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway:400,500i&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.link{
    color: white;
    font-size: 1em;
    padding: 0 2em;
    text-decoration: none;
    background-color:  #2E6296;
    border: none;
}

.link:hover{
    cursor: pointer;
    color: #FE5F55;
}

.link:active, .link:focus{
    outline: none;
}

li{
    list-style-type: none;
}

#right-links li{
    border-right: 2px solid #FE5F55;
}

#right-links li:last-child{
    border-right: none;
}
#top-navbar { font-family: 'Open Sans', sans-serif;
    font-size: 1.1em;
    width: 100%;
    margin: 0;
    display: flex;
    position: fixed;
    padding: 1.5em 0;
    background-color: #2E6296;
    color: #BDD5EA;
}

#left-link li a{
    color: white;
    font-family: 'Permanent Marker', cursive;
    font-size: 1.2em;
}

#right-links{
    display: flex;
    margin: 0;
    padding: 0;
    margin-left: auto;
}

#profile-container{
    height: 80vh;
    padding-top: 7em;
    display: flex;
    align-items: center;
    padding: 3em 5em;
    background-color: #2E6296;
}

#profile-container .halfDivision{
    flex: 1 1;
}

#img-container{
    display: flex;
    max-height: 90%;
    max-width: 38%;
    justify-content: center;
}

#img-container img{
    max-height: 100%;
    max-width: 100%;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

#profile-container .halfDivision p{
    font-family: 'Poppins', sans-serif;
    font-size: 6em;
    color: white;
    margin: 0;
}

#profile-container .halfDivision #mini-container p{
    font-family: 'Poppins', sans-serif;
    font-size: 1.1em;
    color: #FE5F55;
    padding: 0.5em 0;
}

#nameTitle{
    margin-left:10em;
}

#profile-pic{
    max-width: 100%;
    height: auto;
}
#personal-container{
    height: 100vh;
    display: flex;
    text-align: center;
    flex-direction: column;
    padding: 0 10em;
    margin-top: 3em;
}

#content-container{
    display: flex;
    text-align: left;
    margin-top: 2em;
}

#content-container .info-header{
    text-align: center;
}
.info-elem-container{
    display: flex;
    align-items: center;
    font-size: 1.2em;
    margin: 1em 0;
}

.info-elem-container i{
    font-size: 1.4em;
}

.info-elem-container p{
    margin-left: 1em;
}

#personal-info-container{
    flex: 1 1;
}

#top-skills-container{
    flex: 1 1;
    color: '#08415C'
}
.double-skills-container{
    display: flex;
}

.double-skills-container .skill-container{
    flex: 1 1;
}

.double-skills-container .left-container{
    border-right: 1px solid black;
    margin-right: 1em;
}

.double-skills-container .right-container{
    text-align: right;
    border-left: 1px solid black;
    margin-left: 1em;
}
#portfolio-container{
    padding: 0 10em;
    text-align: center;
}


.project-container{
    width: 100%;
    text-align: left;
}

.project-container i{
    font-size: 2em;
}

.project-container .date-container{
    border: 2px solid gray;
    border-radius: 10px 10px 0 0;
    border-bottom: none;
    width: 20%;
    display: flex;
    padding: 0.8em 0;
    margin-top: 1em;
    justify-content: center;
    align-items: center;
    background-color: #E8E8E8;
}

.project-container .date-container p{
    margin: 0 1em;
}

.project-container .content-container{
    display: flex;
    padding: 1em 1em;
    border: 2px solid gray;
    border-radius: 0 10px 10px 10px;
}

.project-container .content-container .text-container{
    flex-grow: 2;
    padding-right: 1em;
}

.project-container .content-container .text-container .description-container{
    display: flex;
}

.project-container .content-container .text-container .description-container p:last-child{
    padding-left: 0.7em;
    word-break: break-all;
}

.project-container .content-container .img-container{
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 20em;
}

.project-container .content-container .img-container img{
    max-width: 100%;
    max-height: 100%;
}

#contact-container{
    text-align: center;
    padding-right: 2em;
    padding: 0 10em;
    height: 100vh;
    background-color: #E8E8E8;
}

#contact-container h1{
    margin-top: 3em;
    color: #084C61;
}

#contact-container p{
    margin: 3em 0;
}

#contact-container p:nth-child(4){
    margin-top: 8em;
}

#email-container{
    display: inline-block;
    font-size: 1.3em;
    color: black;
    text-decoration: none;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    padding: 3em 7em;
    border: 2px solid #084C61;
}

#email-container::after{
    -webkit-transition: all 0.3s ease-in;
    transition: all 0.3s ease-in;
    content: 'SHOW ME THE EMAIL';
}

#email-container:hover::after{
    content: 'franciscomaria97@hotmail.com';
}

body{
  font-family: 'Raleway', sans-serif;
  max-width: 100%;
  margin: 0;
  min-width: 1300px;
}
