#contact-container{
    text-align: center;
    padding-right: 2em;
    padding: 0 10em;
    height: 100vh;
    background-color: #E8E8E8;
}

#contact-container h1{
    margin-top: 3em;
    color: #084C61;
}

#contact-container p{
    margin: 3em 0;
}

#contact-container p:nth-child(4){
    margin-top: 8em;
}

#email-container{
    display: inline-block;
    font-size: 1.3em;
    color: black;
    text-decoration: none;
    transition: all 0.2s ease-in-out;
    padding: 3em 7em;
    border: 2px solid #084C61;
}

#email-container::after{
    transition: all 0.3s ease-in;
    content: 'SHOW ME THE EMAIL';
}

#email-container:hover::after{
    content: 'franciscomaria97@hotmail.com';
}